.section .gallery--section
{
    display: flex; width: 65%; flex-wrap: wrap; min-height:60vh;
    padding:30px;

}
#gallery
{
min-height: 100vh;
justify-content: center;
}
.section #title
{
    font-size:3em;
    color:crimson;
}
.section .gallery--section .gallery
{
    height: 250px;
    width: 50%;
    overflow: hidden;
}
.section .gallery--section .gallery .image,
.section .gallery--section .gallery .image img
{
    height: 100%; width:100%; object-fit: cover;
}

.section .gallery--section .gallery:nth-child(1) .image
{
    transform: translateY(-100%);
    transform-origin: top;
    transition: 3s linear;
}

.section .gallery--section .gallery:nth-child(2) .image
{
    transform: translateX(100%);
    transform-origin:left;
    transition: 3s linear;
}
.section .gallery--section .gallery:nth-child(3) .image
{
    transform: translateX(-100%);
    transform-origin:right;
    transition: 3s linear;
}

.section .gallery--section .gallery:nth-child(4) .image
{
    transform: translateY(100%);
    transform-origin: bottom;
    transition: 3s linear;
}
.section.active .gallery--section .gallery .image
{
transform: translate(0,0);
}

@media screen and (max-width:900px)
{
    #gallery .gallery--section
    {
        width: 100%;
    }
}

@media screen and (max-width:600px)
{
    #gallery .gallery--section
    {
        width:80%;
    }
    #gallery .gallery--section .gallery
    {
        width: 100%;
    }
}

@media screen and (max-width:400px)
{   

    #gallery .gallery--section
    {
        width:100%;
        padding:0;
        margin-top:30px;
    }
} 