*{margin:0; padding:0; box-sizing: border-box; font-family: sans-serif;}

body::-webkit-scrollbar {
    width: 5px; }
  
  body::-webkit-scrollbar-track {
    border-radius: 20px;
    background:transparent;
     }
  
  body::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background: crimson; }

.section
{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
 
 }
#header {
    background-image:url('./1.jpg');
      width: 100%; min-height: 100vh; background-size: cover; background-repeat: no-repeat;
    background-attachment: fixed; 
    animation :play2 20s ease infinite;
    animation-delay:7s;
    position: relative;
    overflow: hidden;
}

#header::after{content:''; position: absolute; width: 100%; height: 100%; 
top:0; left:0; background:#000; opacity: .8; z-index: 0;}

#header .navbar {
    z-index: 4; display: flex; justify-content: space-between;  width: 70%; padding:10px; align-items: center;
    position:fixed;
    top:0;
    
}


#header .navbar #menu {
    display: flex; list-style: none; flex-wrap: wrap; justify-content: space-around;
}

#header .navbar #menu li 
{
    margin-right: 20px;
    display: block;
    height: 30px;
    transform:translate(1000px);
    animation: land 0.5s linear forwards;
    animation-delay:var(--delay);
}
#header .navbar #menu li:nth-child(6)
{
    margin:0;
} 
#header .navbar #menu li a
{
    position: relative;
    overflow: hidden; color:transparent;
    cursor:pointer;
    display: block;
    line-height: 30px;
    font-size: 1.2em;
}
#header .navbar #menu li a::before{ content:attr(data-text); width:100%; height: 100%; position: absolute; top:100%; left:0; color:crimson; transition:0.5s;}
#header .navbar #menu li a::after{ content:attr(data-text); width:100%; height: 100%; position: absolute; top:0; left:0; color:#fff; transition:0.5s;}

#header .navbar #menu li:hover a::after,
#header .navbar #menu li .active::after
{ top:-100%;}
#header .navbar #menu li:hover a::before,
#header .navbar #menu li  .active::before
{ top:0;}

#header .navbar #logo {
    color: #fff; font-size: 2em;
    transform: translate(-400px);
    animation: land 0.5s linear forwards;
}
#header .navbar #logo span{color:crimson;}

#header .navbar #toggle
{
    position: fixed;
    right:20px;
    top:10px;
    height: 30px;
    background:#fff;
    width: 30px;
    display:none;
}

#header .navbar #toggle span
{
    position: absolute; height: 3px; width:25px;
    top:calc(50% - 1.5px); left:calc(50% - 12.5px); background:crimson; display: block;
    border-radius: 4px;
}
#header .navbar #toggle span:nth-child(1)
{
    transform:translateY(-8px) ;
    transition:0.5s;
}
#header .navbar #toggle span:nth-child(3)
{
    transform:translateY(8px) ;
    transition:0.5s;
}
#header .navbar #opener:checked ~ #toggle span:nth-child(1)
{
    transform:rotate(45deg);
}
#header .navbar #opener:checked ~ #toggle span:nth-child(2)
{
opacity: 0;
} 

#header .navbar #opener:checked ~ #toggle span:nth-child(3)
{
    transform:rotate(-45deg);
}

#header .navbar.change
{
    background:#fff;
    width: 100%;
    justify-content: center;
    z-index: 5;
}


#header .navbar.change #menu
{
width:80%;
justify-content: center;
}

#header .navbar.change #menu li a::after
{
    color:#000;
}
#header .navbar.change #logo
{
    color:#000;
}
#header .navbar.change #logo span
{
    color:crimson;
}

#header .banner
{
    display: flex;
    width: 70%;
    justify-content: center;
    align-items: center;
    z-index: 1;
    margin-top:40px;
    min-height: 90vh;
}

#header .banner .banner--part
{
    width: 50%; display: flex; flex-direction: column; flex-wrap: wrap;  align-items: center; 
}

#header .banner .slogan h1
{
    font-size:2.5em;
    overflow: hidden;
    margin-top:10px;
}
#header .banner .slogan h1 span
{
    position: relative;
    color:transparent;
}
#header .banner .slogan h1  span:nth-child(1)::after
{
    content:attr(data-text); width:100%;  position: absolute; top:0; left:0; color:#fff; transition:0.5s;
    transform: translateY(-100px);
    animation: land 1s linear forwards;
    animation-delay: 1s;
}

#header .banner .slogan h1 span:nth-child(2)::after
{
    content:attr(data-text); width:100%;  position: absolute; top:0; left:0; color:crimson; transition:0.5s;
    transform: translateX(300px);
    animation: land 1s linear forwards;
    animation-delay: 2s;
}

#header .banner .slogan p
{
    margin-top:10px;
    position: relative;
    overflow: hidden;
    color:transparent;
    font-size:1.6em;
}
#header .banner .slogan  p::after
{
    content:attr(data-text); width:100%;  position: absolute; top:0; left:0; color:#fff; transition:0.5s;
    transform: translateY(-100px);
    animation: land 1s linear forwards;
    animation-delay: 3s;
    
}
#header .banner .slogan .button
{
    margin-top: 30px;
    width: 100px;
    height: 30px;
    display: block;
    position: relative;
    
}
#header .banner .slogan .button label 
{
    position: absolute; width: 100%; height: 100%;
    display: block;
    overflow: hidden;
}
#header .banner .slogan .button label::after{
    content: ''; position: absolute; width: 100%; height: 100%;
    background:#fff; display: block;
    transform: translateX(-100px);
    animation: land 1s linear forwards;
    animation-delay: 4s;
}
#header .banner .slogan .button span
{
    z-index: 2;
}

#header .banner .slogan .button span:nth-child(1)
{
    background:transparent; position:absolute; top:-5px; left:5px; width:100%; height:100%;
    display: block;
    overflow: hidden;
}
#header .banner .slogan .button span:nth-child(1)::after{
    content:'';
    background:crimson; position:absolute; top:0; left:0; width:100%; height:100%;
    transform: translateX(-100px);
    animation: land 1s linear forwards;
    animation-delay: 5s;
    display: block;
}
#header .banner .slogan .button span:nth-child(2)
{
    position: absolute;
    position:absolute;top:-5px; left:5px; width:100%; height:100%; text-align: center; line-height: 30px;
    color:transparent;
    overflow: hidden;
}
#header .banner .slogan .button span:nth-child(2)::after
{
    content:attr(data-text); width:100%;  position: absolute; top:0; left:0; color:#fff; transition:0.5s;
    transform: translateY(100px);
    animation: land 1s linear forwards;
    animation-delay: 6s;
}
#header .banner .slogan .button:hover span:nth-child(1){left:0; top:0;}
#header .banner .slogan .button:hover span:nth-child(2){left:0; top:0;}




#header .banner .slider .slider--box
{
height: 300px;
width: 200px;
display: block;
position: relative;
overflow: hidden;
}

#header .banner .slider .slider--box::after
{  content: '';
    top:0; left:0; position:absolute; box-sizing: border-box;
    width: 100%;
    height: 100%;
    background:#fff;
    opacity: .5;
    z-index: 1;
    transform: translateY(100%);   
    animation: land 1s linear forwards;
    animation-delay: 7s;
}

#header .banner .slider .slider--box .slider--content
{
    top:0; left:0; position:absolute; 
    width: 200%;
    height: 200%;
    display: flex; flex-wrap: wrap;
    transform: translateY(100%);
    transition:0.5s;
    animation :play 20s ease infinite;
    animation-delay:7s;
    z-index: 2;
}
#header .banner .slider .slider--box .slider--content .slide
{
    
height:50%;
width: 50%;

display: flex; justify-content: center; align-items: center;   border:5px solid transparent;
}

#header .banner .slider .slider--box .slider--content .slide img
{   
    height:100%;
    width: 100%;
     object-fit: cover;
}
@keyframes land
{
    from{ }
    to{transform: translate(0); opacity:1;}
}

@keyframes play {
    0% {
      top: 0;
      left: 0; transform:translate(0) ; }
    20% {
      top: 0;
      left: 0;  transform:translate(0) ; }
    25% {
      top: 0;
      left: -100%; transform:translate(0) ; }
    45% {
      top: 0;
      left: -100%; transform:translate(0) ; }
    50% {
      top: -100%;
      left: -100%; transform:translate(0) ;  }
    70% {
      top: -100%;
      left: -100%; transform:translate(0) ; }
    75% {
      top: -100%;
      left: 0; transform:translate(0) ; }
    95% {
      top: -100%;
      left: 0; transform:translate(0) ; }
    100% {
      top: 0;
      left: 0; transform:translate(0) ;  } }

      @keyframes play2 {
        0% {
          background-image: url('./1.jpg');
        }
        20% {
            background-image: url('./1.jpg');
           }
        25% {
            background-image: url('./2.jpg');
          }
        45% {
            background-image: url('./2.jpg');
          }
        50% {
            background-image: url('./4.jpg');
          }
        70% {
            background-image: url('./4.jpg');
           }
        75% {
            background-image: url('./3.jpg');
          }
        95% {
            background-image: url('./3.jpg');
           }
        100% {
            background-image: url('./1.jpg');
          } }

       #header  .social
       {
           display:flex;
           flex-wrap: wrap;
           width: 100%;
           justify-content: center;
           align-items: center;
           z-index: 2;
       }   
      

         @media screen and (max-width:950px)
         {
             #header .navbar,
             #header .banner
             {
                 width: 95%;
             }
         } 
         @media screen and (max-width:699px)
         {
            #header .navbar
            {
                z-index: 4;
                width: 100%;
                position: sticky;
                top:0;
              
            }
            #header .navbar #toggle
            {
                display: block;
                background:none;
            }
             #header .navbar #menu
             { 
                padding:20px;
                 position: absolute;
                 left:0;
                 top:-300px;
                 flex-direction: column;
                 align-items: center;
                 width: 100%;
                 background:#fff;
                 transform-origin: top;
                 transition: 0.5s;
                 border-radius: 0 0 50% 50%;
                

                 
             }

             #header .navbar #menu li a::after
{
    color:#000;
}
            
             #header .navbar #opener:checked ~ #menu
             {
                 top:0;
             }
             #header .banner
             {
                min-height: 100vh; 
                 width: 95%;
                 flex-wrap: wrap;
                 flex-direction: column;
             }
             #header .banner .banner--part{ width: 100%;}
             #header .banner .slogan {
                 justify-content: center;
                 margin-top:20px;
             }
            
         } 