#strategy
{
    min-height: 100vh;
}
#strategy h1
{
    color:#000;
    font-size: 3em;
}

.objective--strength
{
    display:grid;
    grid-template-columns:repeat(auto-fit,minmax(250px,1fr)); 
    width: 70%;
    grid-gap: 10px;
    margin-top:40px;
   
    
}

.objective--strength .box
{
    padding: 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    height: 320px;
    transform:translateY(100px);
   transition:0.5s;
   opacity: 0;
}
.objective--strength .box .fa
{
   
    font-size: 5em;
    color:#fff;
    text-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px #eee,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px #eee;

}
.objective--strength .box h3
{
    margin-top: 10px;
    color:#222;
    font-size: 2em;
    text-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px #222,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px #222;
}
.objective--strength .box p
{
    margin-top: 10px;
    font-size: 1.2em;

}

.section.active .objective--strength .box
{
    transform:translate(0);
    opacity:1;
}

.objective--strength .box:nth-child(1) .fa
{
    color:orangered;
}

.objective--strength .box:nth-child(3) .fa
{
    color:green;
}