
#about
{
    min-height: 100vh;
justify-content: center;
overflow: hidden;

}

#about h1
{
     font-size: 3em;
     margin-top: 30px;
}

.content--box
{   
    display:grid;
    grid-template-columns:repeat(auto-fit,minmax(200px,1fr)); 
    margin-top:30px;
    grid-row:300px;
    width: 70%;
   
}



.content--box .img-box
{
    height: 200px;
    width: 200px;
    position: relative;
}

.content--box .img-box .profile
{  
    top:0;left:0;
    width: 100%; height: 100%; position: absolute; overflow: hidden;
}

.content--box .img-box .profile img
{
    width: 100%; height: 100%; object-fit: cover; position:absolute; top:0; left:0; z-index: 2;
    transform: translateY(100%);
    transition:2s;
    transition-delay: 1s;
}

#about.active .content--box .img-box .profile img
{
transform: translate(0);
}

.content--box .img-box .line--border
{
    position:absolute; width: 100%; height: 100%;
    box-sizing: border-box;
     border-left:8px solid crimson;
     border-right:8px solid crimson;
     z-index: 1;
     opacity: 0;
     transition: 0.5s;
   
}

.content--box .img-box  .cover--border
{   
 
    position:absolute; width:100%; height: 100%;
    left:0; top:0; 
    box-sizing: border-box;
     border-bottom:8px solid crimson;
     border-top:8px solid crimson;
     z-index: 3;
     opacity: 0;
     transition: 0.5s;
}
#about.active .content--box .img-box  .cover--border,
#about.active .content--box .img-box  .line--border
{
opacity:1;    
transform: rotate(30deg);
}
.content--box .box  .data
{
    display: flex;
    position: relative;
    width: 100%;
    margin-top:20px;
    
}

.content--box .box  .data .icon
{
    position: relative;
    width: 40px;
    height: 40px;
    overflow: hidden;
    margin-right:4px;
}
.content--box .box  .data .icon i
{
    position: absolute; width: 100%; height: 100%;
    top:0; left:0; line-height: 40px; text-align: center;
    background:crimson;
    font-size: 1.4em;
    color:#fff;
    transform: translateX(100%);
    transition:0.5s;
   
}
.content--box .box  .data .content{
    position: relative;
    overflow: hidden;
}
.content--box .box  .data  .content h3,
.content--box .box  .data .content p
{
    transform: translateX(100%);
    transition:2s;
    transition-delay:2s;
}

#about .summary
{
    width: 40%;
    text-align: center;
    margin-top:50px;

    font-size: 1.2em;
    font-weight: 200;
    transform: translateY(500px);
    transition:2s;
    transition-delay:4s;

}
#about .summary p::first-letter
{
    color:crimson;
    font-size:2em;
 
}

#about .summary #modal
{
    border:none; outline:none; height: 30px; width: 100px;
    color:#fff; background:crimson;
    margin-top:20px;
}

#about.active .summary{
    transform: translateY(0);
}

#about.active .content--box .box  .data .icon i { transform: translate(0);}

#about.active .content--box .box  .data  .content h3,
#about.active .content--box .box  .data .content p
{
transform: translate(0);
}

@media screen and (max-width:1070px)
{
    .content--box
{   
    margin-top:40px;
    align-items: center;
    grid-template-columns:repeat(auto-fit,minmax(350px,auto));
    grid-row-gap: 30px; 
    width: 90%;
}


}
@media screen and (max-width:540px)
{


    .content--box
{   
    margin-top:40px;
    grid-template-columns:repeat(1,1fr); 
    width:auto;
}
#about .summary
{
    width: 90%;
}


}