#contact
{

    justify-content: center;
    min-height: 100vh;
}
.section #title
{
    text-shadow:0.5px 0.5px 1px rgba(0,0,0,.5),0.5px 1px 1px crimson;
    margin-top:50px;
}

#contact .contact--section
{
display: flex;
width: 70%;
justify-content: center;
align-items: center;
overflow: hidden;
}

#contact .contact--section .contact--part
{
    width: 50%; display: flex; 
    justify-content: center; 
    align-items: center;
    flex-direction: column;
    

}
#contact .contact--section .contact--part #animate
{
    
    position: relative;

}

#contact .contact--section .contact--part #animate
{
    transform: translateX(-400%);
    transition:0.5s;
}
#contact .contact--section .contact--part #animate span{
    display: block;
    float: left;
    font-size: 5em;
    overflow: hidden;
    position: relative;
    font-weight: 800;

}
#contact .contact--section .contact--part #animate span:nth-child(2)::after
{
    content:''; position: absolute; 
    height: 5px;
    width:100%;
    left:0;
    top:calc(50% - 2.5px);
    display: block;
    background:crimson;
    transform:scaleX(0);
    transform-origin:left;
    transition:0.5s;
    transition-delay:0.5s;
}

#contact .contact--section .contact--part #animate span:nth-child(4)::after
{
    content:''; position: absolute; 
    height:5px;
    width:100%;
    left:0;
    top:50%;
    display: block;
    background:crimson;
    transform:scaleX(0);
    transform-origin:right;
    transition: 0.5s;
    transition-delay: 0.5s;
}
#contact.active .contact--section .contact--part #animate span::after
{
    transform:scaleX(1);
}
#contact.active .contact--section .contact--part #animate span:nth-child(2),
#contact.active .contact--section .contact--part #animate span:nth-child(4)
{
    font-size: 3em;
   
    transition:0.5s;
    transition-delay:1s;
}
#contact.active .contact--section .contact--part #animate span:nth-child(2)
{
transform:translateY(30px);
}
#contact.active .contact--section .contact--part #animate span:nth-child(4)
{
transform:translateY(10px);
}
#contact.active .contact--section .contact--part #animate
{
transform:translateX(0)
}

#contact.active .contact--section .contact--part .field
{
    margin-top:10px; display:flex;
    width: 100%;
    flex-direction: column;
}
#contact.active .contact--section .contact--part .field h3{ font-weight: 600;}

#contact.active .contact--section .contact--part .field .input
{
    width:300px;
    display: flex;
    margin-top:5px;
    background:#444;
 

}


#contact.active .contact--section .contact--part .field .input input{
    outline:none;border:none;
    background:none;
    width: 100%;
    margin-left:4px;
    color:#fff;
}

#contact.active .contact--section .contact--part .field .input  i{
    height:40px; width:40px;
    line-height: 40px;
    text-align: center;
    color:#fff;
    background:crimson;
    box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px crimson,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px crimson;
    display: block;
}

#contact.active .contact--section .contact--part .button
{
    margin-top:20px;
    display: flex;
    width: 100%;

}
#contact.active .contact--section .contact--part .button  button
{
    height: 30px;
    border:none;
    margin-right: 4px;
    outline:none;
    font-size: 1.4em;
    font-weight: 600;
    width: 120px;
}

#contact.active .contact--section .contact--part .button  button:nth-child(1)
{
    color:crimson;
    text-shadow:0.5px 0.5px 1px rgba(0,0,0,.5),0.5px 1px 1px crimson;
    background:#444;
    box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px #eee,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px #eee;
}
#contact.active .contact--section .contact--part .button  button:nth-child(2)
{
color:#fff;
background:crimson;
box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px crimson,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px crimson;
}

#contact.active .contact--section .contact--part .button  button:hover{
    box-shadow: none;
}

#contact .contact--section .contact--part:nth-child(2)
{
    transform: translateY(600px);
    transition: 0.5s;
    transition-delay:1.5s;
}
#contact.active .contact--section .contact--part:nth-child(2)
{
transform:translate(0);
}

#contact .social
{
    display: flex;
    margin-top:30px;
    width: 100%;
    margin-bottom:40px;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
#contact .social a
{
    margin:10px;
    height: 30px;
    width: 30px;
    line-height: 30px;
    text-align:center;
    display:block;
    font-size:1.2em;
    border-radius:50%;
    transform: translateY(200%);
    transition:0.5s;

}
#contact .social a:nth-child(1)
{
    background:#fff;
    color:blue;
    box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px #eee,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px #eee;
    transition-delay:2s;

}
#contact .social a:nth-child(2)
{
    background:cyan;
    color:#fff;
    box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px cyan,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px cyan;
    transition-delay:2.5s;

}
#contact .social a:nth-child(3)
{
    background:#ff0066;
    color:#fff;
    box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px #ff0066,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px #ff0066;
    transition-delay:3s;
}
#contact .social a:nth-child(4)
{
    background:orangered;
    color:#fff;
    box-shadow:-0.5px 0.5px 2px rgba(0,0,0,.5),-0.5px 1px 2px orangered,-0.5px 1.5px 2px rgba(0,0,0,.5),-0.5px 2px 2px orangered;
    transition-delay:3.5s;
}

#contact.active .social a
{
    transform:translate(0)
}

@media screen and (max-width:900px)
{
    #contact .contact--section 
    {
        width: 100%;
    }
}

@media screen and (max-width:700px)
{  
    #contact .contact--section 
    {
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
    }
    
    #contact .social
    {
        margin-top:30px;
    }
}

