#qualification
{
    min-height:100vh;
    justify-content: center;
    align-items: center;
}


#qualification h
{
    margin-top:20px;
    font-size:2.5em;
    transform:translateY(50px);
    transition:0.5s;
    opacity: 0;
}

#qualification p
{
    font-size:1.5em;
    transition:0.5s;
    transition-delay:0.5s;
    
}

#qualification p
{
    transform: translateY(40px);
    opacity:0;
} 



#qualification.active h,
#qualification.active p{
    transform:translate(0);
    opacity: 1;
} 