#skills
{
    min-height: 100vh;
    justify-content: center;
}
#skills .skills--container
{
    width:60%;
    display: flex;
    justify-content:space-around;
    align-items: center;
    flex-wrap: wrap;
    min-height: 60vh;
    margin-top:40px;

}
#skills .skills--container .skills--part
{
    width: 45%;
    display: flex;
    flex-direction: column;
    height: 350px;
    overflow: hidden;
    
}   
#skills .skills--container .skills--part h2
{
transform: translateX(100%);
transition:0.5s;
} 

#skills .skills--container .skills--part:nth-child(2) h2
{
    transition-delay:1;
}

#skills .skills--container .skills--part .content
{
    transform: translateY(400px);
    transition:0.5s;
}
#skills .skills--container .skills--part:nth-child(1) .content
{
transition-delay: 0.5s;
}


#skills .skills--container .skills--part:nth-child(2) .content
{
transition-delay: 1.5s;
}

#skills .skills--container .skills--part .content .technology
{
    width: 100%;
    margin-top:10px;
    display: flex;
    flex-direction: column;
}
#skills .skills--container .skills--part .content .technology label
{
    font-weight: 600;
}

#skills .skills--container .skills--part .content .technology .process-bar
{
 width: 100%;
 height:4px;
 margin:5px 0;
 display: block;
 position: relative;
 background:#eee;
}

#skills .skills--container .skills--part .content .technology .process-bar .water
{
    position:absolute; top:0; left:0; height: 100%; display: block;
}

#skills.active .skills--container .skills--part .content,
#skills.active .skills--container .skills--part h2
{
transform: translate(0);
}

@media screen and (max-width:900px)
{
    #skills .skills--container
    {
        width: 100%;
    }
}

@media screen and (max-width:700px)
{ 
    #skills .skills--container .skills--part
    {
        width: 90%;
    }
}